.react-article-source img {
    display: block;
    margin-top: 16px;
    margin-bottom: 16px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    object-fit: cover;
    max-width: 100%;
    text-align: center;
    max-height: 360px;
}

.react-article-source p {
    overflow: hidden;
    overflow-wrap: break-word;
    word-wrap: break-word;
}